@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,400;0,500;0,600;0,700;1,100&display=swap');
@import '../login/login.scss';
html,body{
  font-family: 'Raleway',sans-serif;
  padding:0;
  margin:0;
  overflow: hidden;
}
a{
  text-decoration: none;
  color:white;
  font-size: 20px;
}
.signUp{
  display:flex;
  justify-content: center;
  align-items: center;
  height:100vh;
  width:100vw;
  background: center / cover no-repeat url("../../assets/Background.png");
  &__container{
    max-height: 90vh;
    color:white;
    display:flex;
    flex-direction: column;
    align-items: center;
  } 
  &__form{
    display: flex;
    flex-direction: column;
  }
}
.custom-input{
  @include custom-input-button();
  height:60px;
  font-size: 20px;
  &:first-of-type{
    margin-top: 25px;
  }
  margin-bottom:20px;
  &:last-of-type{
    margin-bottom: 40px;
  }
}
.custom-button{
  @include custom-input-button();
  background-color: #DE6C69;
  border:none;
  color:white;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  transition: 0.3s ease all;
  cursor:pointer;
  &:hover{
    transform:translateY(-3px)
  }
}